function createTextMessage(data) {
  return `
    <div class="meta">
      <span class="time">${prettifyISOString(data.createdAt)}</span>
      <span
        class="username"
        style="color: ${data.color};"
      >${data.username}</span>
    </div>
    <p class="content">${convertToLink(data.content)}</p>
  `;
}

function createImageMessage(data) {
  return `
    <div class="meta">
      <span class="time">${prettifyISOString(data.createdAt)}</span>
      <span
        class="username"
        style="color: ${data.color};"
      >${data.username}</span>
    </div>
    <image class="content" src="${data.url}">
  `;
}

function convertToLink(content) {
  // TODO: Ïù∏ÏûêÎ°ú Ï†ÑÎã¨ÎêòÎäî ÌÖçÏä§Ìä∏ ÎÇ¥Ïö© Ï§ëÏóê http:// ÌòπÏùÄ https:// Î°ú ÏãúÏûëÌïòÎäî ÌÖçÏä§Ìä∏Îäî a ÌÉúÍ∑∏Î•º Ïù¥Ïö©ÌïòÏó¨ ÎßÅÌÅ¨Î°ú Î≥ÄÌôòÌï¥Ï£ºÏÑ∏Ïöî.
  // Ï†ïÍ∑úÌëúÌòÑÏãùÏùÑ ÏÇ¨Ïö©ÌïòÏßÄ ÎßàÏÑ∏Ïöî.
  // content: "Ïó¨Í∏∞ÏÑú Î≥¥ÏÖîÎèÑ ÎèºÏöî https://i.ibb.co/Mnz4ysz/i-OS.jpg"
  var t = 0;
  var p; 
  var q;
  var result = '';
  while(1) {
    p = content.indexOf('http', t);
    if (p == -1) break;
    // t ~ p-1ÍπåÏßÄÎäî text
    var text = content.substring(t, p);
    q = content.indexOf(' ',p+1);
    // p ~ qÍπåÏßÄÎäî link
    var link = content.substring(p, q);
    var hyperLink = '<a href=' + link + '>' + link + '</a>';
    var fraction = text + hyperLink;
    result += fraction;
    t = q;
  }
  return result;
}

function prettifyISOString(iso) {
  // TODO: Ïù∏ÏûêÎ°ú Ï†ÑÎã¨ÎêòÎäî ISO StringÏùÑ "ÏãúÍ∞Ñ:Î∂Ñ" ÌòïÌÉúÎ°ú Î≥ÄÌôòÌïòÏó¨ Î∞òÌôòÌïòÏÑ∏Ïöî. Ïòà) "20:13"
  // Ï†ïÍ∑úÌëúÌòÑÏãùÏùÑ ÏÇ¨Ïö©ÌïòÏßÄ ÎßàÏÑ∏Ïöî.
  // createdAt: "2021-07-10T11:58:09.000Z"
  var dotIndex = iso.indexOf(':');
  return "20:13";
}

const messageListElement = document.querySelector(".message-list");

for (let i = 0; i < CHAT_HISTORY.length; i++) {
  const chat = CHAT_HISTORY[i];

  const messageElement = document.createElement("li");

  if (chat.type === "text") {
    messageElement.innerHTML = createTextMessage(chat);
  } else if (chat.type === "image") {
    messageElement.innerHTML = createImageMessage(chat);
  }

  messageElement.classList.add("message");
  messageListElement.appendChild(messageElement);
}
